import '@babel/polyfill' //配合使用 <v-icon>
import Vue from 'vue'
import Vuetify from 'vuetify'
import router from './router' //使用 vue-router
import App from './App'
import 'vuetify/dist/vuetify.min.css' //使用 vuetufy 的 css 樣式
import colors from 'vuetify/es5/util/colors' //使用 vuetify 的色碼名稱
import '../style/style.scss' //引用自訂 SCSS

import Vuex from 'vuex'

Vue.use(Vuex)
Vue.use(Vuetify, {
  theme: {
    //自訂所以組件的標準樣式
    primary: colors.red.darken1, // #E53935
    secondary: colors.red.lighten4, // #FFCDD2
    accent: colors.indigo.base, // #3F51B5
    error: '#b71c1c'
  }
})
Vue.config.productionTip = false

//建立store
const store = new Vuex.Store({
  state: {
    todos: [
      { id: 1, text: '...', done: true },
      { id: 2, text: '...', done: false }
    ]
  },
  mutations: {
    ///
  },
  getters: {
    doneTodos: state => {
      return state.todos.filter(todo => todo.done)
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
