<template>
  <div class="viewWidth">
    <div class="center">
      <div class="title">
        <h1>Vue</h1>
        <p>前端框架、router-link</p>
        <a href=""></a>
      </div>
      <div class="directory">
        <h2>目錄</h2>
        <ul>
          <li>props 父傳子
            <ul>
              <li>父傳子資料類型</li>
              <li>父傳子使用方式</li>
            </ul>
          </li>
          <li>vue-router 的相關配置?
            <ul>
              <li>常見應用與API?</li>
              <li>vue-router 樣式的設計?</li>
              <li>轉址與別名的設計?</li>
              <li>巢狀路由的設計?</li>
              <li>組件使用 router-link 的兩種方式 ?</li>
            </ul>
          </li>
          <li>如何設定 Vue 與 Vue Router 環境?</li>
        </ul>
        <hr>
      </div>
      <!-- li>h3+(ul>li>pre.prettyprint) -->
      <ul>
        <li>
          <h3>props 父傳子</h3>
          <a
            href="https://codesandbox.io/s/componentstest-9gzlx"
            target="_blank"
          >one_FTC</a>
          <ul>
            <li>
              <h4>1.父傳子資料類型</h4>
              <p>Props 接收外部傳入的方法:type(String、Object、Function)<br>
                type :表示資料型態 (會進行類型檢測，不符合會在控制台發出警告訊息)<br>
                default:設定預設值 (無傳入資料時，使用預設值)<br>
                required: 是否為必輸入屬性 (預設 false)
              </p>
              <pre class="prettyprint">
//目錄
src
|-components
  |-one_FTC
     |-child1.vue
|-views
  |-one_FTC
    |-father1.vue
    |-father2.vue
App.vue
main.js
router.js
//child1.vue
&lt;template>
  &lt;div id="child1">
    &lt;h3>Child1&lt;/h3>
    getString::${getString} //1-使用 props 的 String
    getObject::
    &lt;div v-for="item in getObject" :key="item.id">${item}}&lt;/div>
    //2-使用 props 的 Object
    &lt;a href="#" @click="getFunction">按鈕&lt;/a> 
    //3-使用 props 的 Function 並在子組件內另外使用 @click 去觸發
  &lt;/div>
&lt;/template>
&lt;script>
  export default {
    name: "child1",
    props: {
      getString: {
        type: String, //1-資料類型 String
        default: "default contry", //當尚未接收資料時，顯示預備字串
        required: false //是否一定要輸入字串，flase 否
      },
      getObject: {
        type: Object, //2-資料類型 Object
        default: function() {
          //當尚未接收資料時，顯示預備物件
          return {
            name: "default name",
            description: "default description",
            price: 300.0
          };
        }
      },
      getFunction: {
        type: Function, //3-資料類型 Function
        required: true //是否一定要輸入函數，true 是
      }
    }
  };
&lt;/script>
&lt;style>
  #child1 {
    background-color: rgb(124, 170, 255);
  }
&lt;/style>
//father.vue
&lt;template>
  &lt;div id="father">
    &lt;h3>Father1&lt;/h3>
    &lt;ul>
      &lt;li>toString::${toString}&lt;/li>
      &lt;li>tObject::${toObject}&lt;/li>
    &lt;/ul>
    &lt;Child1 :getString="toString" :getObject="toObject" :getFunction="toFunction">&lt;/Child1>
    //子組件上掛載 1-String、2-Object、3-Function
  &lt;Child1/div>
&lt;/template>
&lt;script>
import Child1 from "@/components/one_FTC/child1.vue";
export default {
  name: "father1",
  components: {
    Child1
  },
  data() {
    return {
      toString: "Your are Good", //1-設定 String
      toObject: { //2-設定 Object
        name: "big Pen",
        description: "What a nice pen",
        price: 99.12
      }
    };
  },
  methods:{
    toFunction(){ //3-設定 Function
     this.toString = 'Good to you'
    }
  }
};
&lt;/script>
&lt;style>
#father {
  background-color: orange;
}
ul {
  list-style: none;
}
&lt;/style></pre>
            </li>
            <li>
              <h4>2.父傳子使用方式</h4>
              <p>直接使用:以上 1.父傳子資料類型，就是直接將 props 接收的資料直接使用<br>
                再次使用:將 props 接收的資料再利用代號使用</p>
              <pre class="prettyprint">
//目錄
src
|-components
  |-one_FTC
     |-child1.vue
|-views
  |-one_FTC
    |-father1.vue
    |-father2.vue
App.vue
main.js
router.js
//child.vue
&lt;template>
 useString::${useString}
&lt;/template>
&lt;script>
export default {
  name: "child1",
  data() {
    return {
      useString: this.getString
      //將 props 接收的資料另外取名稱，才可在子組件中另外使用
    };
  },
  props: {
    getString: {
      type: String,
      default: "default contry", 
      required: false 
    }
  },
  mounted(){
    this.useString = '123'
  }
};
&lt;/script> 
                </pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>vue-router 的相關配置?</h3>
          <a
            href="https://medium.com/@linwei5316/vue-router-4c2aad1cc352"
            target="_blank"
          >Vue Router</a>
          <ul>
            <li>
              <h4>常見應用與API?</h4>
              <p>router.push( location ) 跳轉至指定頁面</p>
              <pre class="prettyprint">
// 情境：登入成功後跳轉頁面
// 來到一個登入頁面組件 login.vue
// 透過 template 中的登入按鈕觸發 router.push( location )
//login.vue
&lt;template>
&lt;button @click="login">login&lt;/button>
&lt;/template>
&lt;script>
export default {
  methods: {
    login(){
      this.$http.post(api, this.user)
        .then( ({data}) => {
          // 以上都不是重點，重點在此
          this.$router.push('/')
          // 透過此方法，可以直接跳轉到根目錄
        })
    }
  }
}
&lt;/script></pre>
            </li>
            <li>
              <h4>vue-router 模式的設計</h4>
              <pre class="prettyprint">
// 來到我們管理路由的檔案router.js
export default new VueRouter({
  mode: 'history',  //若不寫mode就預設為hash模式
  routes: [
    ......
  ]
})</pre>
              <p>如果省略井字號，就是 history 模式。該模式乾淨整潔，但是代價為後端伺服器必須設置 URL Rewrite 重新將路徑指向 index.html ( SPA只在此頁 )。若後端配合好，則 history 模式當然會比較好看，後端無法配合則需採用 hash 模式。<mark>注意：設定為 history 模式，在開發測試環境會自動幫你做到 URL Rewrite，實際上線版本還是要再做設定的不要忘記</mark></p>
            </li>
            <li>
              <h4>轉址與別名的設計?</h4>
              <p>轉址，當匹配了該路徑，會自動'轉換'至指定的路徑。 路由設置中，藉由redirect屬性來達成轉址</p>
              <pre class="prettyprint">
//router.js
// 轉址設定為獨立物件，表示匹配該路徑時，會自動跳轉到另外指定的路徑，路徑也會跟著變換
{
  path: '/aboutus',
  redirect: '/About/us'
}
// 若連結輸入/aboutus
// 自動跳轉至/About/us路由頁面</pre>
            </li>
            <li>
              <h4>巢狀路由的設計?</h4>
              <pre class="prettyprint">
//router.js
{
  path: '/About',
  component: About,
  children: [
    {
      path: 'WTAPS',
      component: WTAPS
    },
    {
      path: 'NBHD',
      component: NBHD
    },
  ]
}
// 此時連結為#/About/WTAPS時
// router-view就會匹配顯示WTAPS組件</pre>
            </li>
            <li>
              <h4>組件使用 router-link 的兩種方式 ?</h4>
              <pre class="prettyprint">
//目錄
App.vue
router.js            
//router.js
import Vue from 'vue'
import Router from 'vue-router'
export default new Router({
routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    }
]
)}
//方式一，使用 path 路徑
//App.vue
&lt;router-link to="/">hone&lt;/router-link>
//方式二，使用 name 為路徑
&lt;router-link :to="{ name: 'home'}">User&lt;/router-link>
//使用在 router.js 內的自訂名稱;名稱就像是 ID 只有唯一性，不可有重複的 name 取名名稱。</pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>如何設定 Vue 與 Vue Router 環境?</h3>
          <ul>
            <li>
              <pre class="prettyprint">
//npm 安裝 Vue 與 Vue Router
$ npm install  -g vue vue-router
//目錄
src
|-views
  |-Home.vue
App.vue
router.js
main.js
//main.js，設定引用套件與建立 Vue 實例
import Vue from 'vue'
import router from './router' //使用自訂 router
import App from './App'

Vue.config.productionTip = false 
//設定 false 以阻止 vue 在啟動時生成生產提示

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
//Home.vue，設定 view 組件畫面
&lt;template>
  &lt;div>
    &lt;h3>Home&lt;/h3>
  &lt;/div>
&lt;/template>
&lt;script>
  export default {
    name: "Home"
  };
&lt;/script>
&lt;style>&lt;/style>
//router.js，設定 router 路徑
import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
  ]
})
//App.vue，設定進入組件
&lt;template>
  &lt;div>
    &lt;h3>App.vue&lt;/h3>
    &lt;div class="container">
      &lt;router-link :to="/">Home&lt;/router-link> //使用vue-router 的路徑
      &lt;Home>&lt;/Home> //引用自訂組件
      &lt;router-view>&lt;/router-view> //顯示 router-link 進來的組件畫面
    &lt;/div>
  &lt;/div>
&lt;/template>
&lt;script>
  import Home from "@/views/Home.vue";
  export default {
    name: "App",
    component: {
      Home
    }
  };
&lt;/script>                
              </pre>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Vue"
  };
</script>

